.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.portfolio {
  margin: 5% 20%;
}

.side {
  position: fixed;
  top: 8%; /* Adjust this value based on your preference */
}

.main {
  margin-left: 350px;
}

@media screen and (max-width: 2000px) {
  .portfolio {
    margin: 7% 11%;
  }
  
}

@media screen and (max-width: 1024px) {
  .portfolio {
    display: flex;
    flex-direction: column;
  }
  
  .side {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;    
  }
  
  .main {
    margin: 0;
  }
}