.aside {
    /* border-right: 1px solid rgba(0, 0, 0, 0.05); */
    width: 250px;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    z-index: 10;
    padding: 0.5rem 0;
}

nav {
    margin: 0;
    padding-top: 25px;

}

.nav__list {
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact {
    margin: 20px 7px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.contact span {
    
}

.contact a {
    margin: 0 10px;
}

.hamburger {
    display: none;
}


