.edu-container {
    display: flex;
    flex-direction: column;
}

.edu-info {
    display: flex;
    flex-direction: row;
    margin-bottom: 15px;
}

#time {
    /* position: fixed; */
    width: 20%;
}

.school { 
    position: relative;
    width: 80%
}

@media screen and (max-width: 800px) {
    .edu-info {
        flex-direction: column;
    }

    #time {
        width: auto;
        margin-bottom: 0.1rem;
    }

    .school {
        width: auto;
    }
  }